import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import {Router} from '@angular/router'
import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ConstantsService } from 'src/app/shared/services/constants.service';

@Component({
  selector: 'app-income-view-credit',
  templateUrl: './income-view-credit.component.html',
  styleUrls: ['./income-view-credit.component.scss']
})
export class IncomeViewCreditComponent implements OnInit {
  
  isCreditNoteLoaded:boolean = false;

  creditNoteData:string = "";
  creditNoteDataList:any;

  custInvoiceDataList:any;
  userDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "invoiceId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  blockUnitData:any;

  apartmentBlockUnitId:any;

  selectedInput:string = "";
  columnField: any = {}

  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    public dialog: MatDialog,
    private userService:UserService,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
    private cookieService:CookieService,
    private constantsService:ConstantsService,
    private router:Router
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  getCustomerName(id){
  
    var data = _.filter( this.userDataList, function( item ) {

      if(item.userId === id)
      return item;

    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName+' '+data[0].lastName;
    }

  }

  getBlockUnitDetails(credit, id){

    var data = _.filter( this.blockUnitData, function( item ) {

      if(item.apartmentBlockUnitId === id)
      return item;

    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.creditNoteDataList.map(obj=> {
        if(obj.invoiceId == credit.invoiceId){
          obj.towerUnit = data[0].apartmentBlockNumber + ' ' + data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockNumber + ' ' + data[0].apartmentBlockUnitNumber;
    }

  }

  getEmailStatus(value){
  	if(value){
  		return 'Y'
  	}
  	else {
  		return 'N'
  	}
  }

  getDate(credit, date){

    this.creditNoteDataList.map(obj=> {
      if(obj.invoiceId == credit.invoiceId){
        obj.postedOn = moment(date).format("DD/MM/YYYY");
      }
    });

  	return moment(date).format("DD/MM/YYYY"); 
  }

  updateCreditItem(credit){


    setTimeout(() => {

      this.isCreditNoteLoaded = false;

      var details = {

        "creditNoteId": parseInt(credit.creditNoteId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "amount": parseInt(credit.amount),
        "collectionId": parseInt(credit.collectionId),
        "invoiceId": parseInt(credit.invoiceId),
        "isEmailSent": credit.isEmailSent,
        "isSmssent": credit.isSmssent,
        "comment": credit.comment,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "transReference1": "string",
        "transReference2": "string",
        "glAcctIndicator": "Income",
        "apartmentBlockUnitId": parseInt(credit.apartmentBlockUnitId)

      }

      let params = {
        creditNote: details
      }

      this.accountsService.updateCreditNote(params).subscribe((res:any) => {

        if(res.message){

          this.isCreditNoteLoaded = true;
          this.sharedService.setAlertMessage("Credit Note updated successfully");

        }
        else {
          this.isCreditNoteLoaded = true;
        }


      },
      error => {
        this.isCreditNoteLoaded = true;
      });

    }, 1000); 

  }

  onGlSearchFilter() {

    if(this.creditNoteData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.creditNoteData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onEditCreditNotes', ['$event.detail'])
  onEditCreditNotes(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let creditNoteId = dataRecord.creditNoteId
    this.router.navigateByUrl('/ams/income/edit-credit/' + creditNoteId);
  }
  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Invoice Id',
      datafield: 'invoiceId',
      width: 100,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Posted By',
      datafield: 'insertedOn',
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    }, {
      text: 'Credit Note Id',
      datafield: 'creditNoteId',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Collection Id',
      datafield: 'c',
      cellsrenderer: cellsrenderer,
      minwidth: 170,
      renderer: columnrenderer
    }, {
      text: 'Posted Date',
      datafield: 'd',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    }, {
      text: 'Tower Unit',
      datafield: 'block_Unit',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    }, {
      text: 'Amount',
      datafield: 'amount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    }, {
      text: 'Sent Email',
      datafield: 'g',      
      minwidth: 80,
      cellsrenderer: (row: number): string => {
        return '<span class="slider round" onClick="updateCreditItem('+ row +')"></span>'
      },
      renderer: columnrenderer
    }, {
      text: 'Sms/Email',
      datafield: 'h',      
      minwidth: 80,
      cellsrenderer: (row: number): string => {
        return '<span class="slider round" onClick="updateCreditItem('+ row +')"></span>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" onClick="editCreditNotes(' + row + ')"><i class="icon fa fa-pencil edit" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];


    this.userDataList = [];

    this.userService.getAllUsers().subscribe((res:any) => {

      this.userDataList = res;

      
    })

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getCreditNotesByApartmentIdStatus(params).subscribe((res:any) => {

  		this.creditNoteDataList = res;

      this.gridSourceData = {
        localdata: this.creditNoteDataList,
        datatype: "array"
      }
      
      this.creditNoteDataList = new jqx.dataAdapter(this.gridSourceData);
      console.log('creditNoteDataList',this.creditNoteDataList)
  		this.totalItems = this.creditNoteDataList.length;

  		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

      this.isCreditNoteLoaded = true;
      
      let params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

      this.accountsService.getCustInvoicesByApartmentId(params).subscribe((res:any) => {

        this.custInvoiceDataList = res.filter(item => {
          return item.isActive;
        });


      }, error => {
          console.log(error);
      });


  	}, error => {
    	console.log(error);
    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res:any) => {

      this.blockUnitData = res;

    });

  }

}

function editCreditNotes(row){
  var event = new CustomEvent('onEditCreditNotes', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editCreditNotes = editCreditNotes;